
import Axios from 'axios'
// const BASE_PATH = 'http://localhost:3003'
const BASE_PATH = (process.env.NODE_ENV !== 'development')
    ? '/api/toy'
    : 'http://localhost:3003/toy'


export default {
    login
  
}


function login(user) {
    console.log(user)
    return Axios.put(`${BASE_PATH}/login`, user)
        .then(loggesUser => loggesUser)
        .catch((err) => err)

}

