
import Axios from 'axios'

// const BASE_PATH = 'http://localhost:3003/toy'

const BASE_PATH = (process.env.NODE_ENV !== 'development')
    ? '/toy'
    : 'http://localhost:3003/toy'


export default {
    query,
    getById,
    remove,
    createEmpty,
    save

}




function query(filterBy) {
    if (filterBy) {
        var queryStr = '?'
        for (var key in filterBy) {
            queryStr += `${key}=${filterBy[key]}&`
        }
    } else queryStr = ''
    return Axios.get(`${BASE_PATH}/${queryStr}`)
        .then(toys => toys.data)
}

function getById(toyId) {
    return Axios.get(`${BASE_PATH}/${toyId}`)
        .then(toy => toy.data)
}

function remove(toyId) {
    return Axios.delete(`${BASE_PATH}/${toyId}`)
        .then(toy => toy.data)
}

function save(toy) {
    if (toy._id) return Axios.put(`${BASE_PATH}/${toy._id}`, toy)
        .then(toy => toy.data)
    return Axios.post(`${BASE_PATH}`, toy)
        .then(toy => toy.data)
}



function createEmpty() {
    const emptyToy = {
        name: '',
        price: 0,
        type: '',
        inStock: true,
        createdAt: ''

    }
    return Promise.resolve(emptyToy)
}