<script>
import { Pie } from 'vue-chartjs'

export default { 
    extends: Pie,

    data() {
      return  {
        toys: [],
        educ: 0,
        adult: 0,
        funny: 0,
        rolePlay:0,
        other: 0
      }
    },


    methods: {
      loadTypeDivision() {
        return this.$store.dispatch({ type: 'loadToys', filterBy: null }) //{ type: 'ToyStore/loadToys', filterBy: null }
          .then(toys => {
            this.toys = toys
            this._getDivision()
          })
      },
      _getDivision() {
        this.toys.forEach(toy => {
          switch (toy.type) {
            case 'Educational': 
              this.educ++
              break
            case 'Adult': 
              this.adult++
              break
            case 'Funny': 
              this.funny++
              break
            case 'Role-play': 
              this.rolePlay++
              break
            default:
              this.other++
          }
        })
      }
    },

    mounted () {
      this.loadTypeDivision()
        .then(() => {
          this.renderChart({
          labels: ['Educational', 'Adult' , 'Funny' , 'Role-playing '],
          datasets: [
            {
              backgroundColor: [
                '#41B883',
                '#E46651',
                '#00D8FF',
                '#DD1B16'
              ],
              data: [this.educ, this.funny, this.adult, this.rolePlay]

            }
          ]
        }, {
          responsive: true, 
          maintainAspectRatio: false,
          pieceLabel: {
            mode: 'percentage',
            precision: 1
          }
        })
        })
          
      

} 
}
</script>
