<template>
  <section class="login">
    <form>
      <el-input class="el-input" name="Username" 
          v-model="user.username" 
          placeholder="Username"
          v-validate="'required'" clearable>
      </el-input>
      <div>{{ errors.first('Username') }}</div>

      <el-input type="password" class="el-input" name="Password" 
          v-model="user.password" 
          placeholder="Password"
          v-validate="'required'" clearable>
      </el-input>
      <div>{{ errors.first('Password') }}</div>
      <el-button type="primary" plain @click="login">login</el-button>

    </form>

  </section>
</template>





<script>

export default {
  name: 'login',

 

  data() {
    return {
      user: {
        username: '',
        password: ''
      }
    }
  },

  methods: {
    login() { 
      if (!this.user.username || !this.user.password) return 
      this.$store.dispatch({ type: 'login', user: this.user})
        // .then(() => this.$router.push('/toy')) 
        // .catch(() => console.log('Can not login. Wrong Credintials'))
    }
  },
 
}
</script>
