<template>
  <section class="filter-group">
    <el-input class="search"
        placeholder="Search Toy"
        clearable
        prefix-icon="el-icon-search"
        v-model="filterBy.name">
    </el-input>
    <el-select v-model="filterBy.type" placeholder="Type">
        <el-option value="" selected>All</el-option>
        <el-option value="Educational">Educational</el-option>
        <el-option value="Funny">Funny</el-option>
        <el-option value="Adult">Adult</el-option>
        <el-option value="RolePlay">Role-play</el-option>
    </el-select>
   
    <el-radio-group class="el-radio-group" v-model="filterBy.inStock" size="small">
        <el-radio class="el-radio" label="all" value="all" checked border >All</el-radio>
        <el-radio class="el-radio" label="true" value="true" border>In Stock</el-radio>
        <el-radio class="el-radio" label="false" value="false" border>Out of Stock</el-radio>
    </el-radio-group>
  </section>
 
</template>

<script>

export default {
  name: 'FilterBy',

  props: {
    filterBy: Object
  },

  watch: {
    filterBy: {
      handler: function() {
        this.$emit('filter', this.filterBy)
      },
      deep: true
      }
  }
}

</script>














