<template>
  <section v-if="currToy" class="toy-details">
    <h1>Toy Details</h1>
    <p>Name: {{currToy.name}}</p>
    <p>Price: {{currToy.price}}</p>
    <p>Type: {{currToy.type}}</p>
    <p>In Stock: {{currToy.inStock}}</p>
    <p>Created at: {{currToy.createdAt}}</p>
    <router-link :to="'/toy/edit/'+currToy._id">
      <el-button type="warning" plain>Edit</el-button>
    </router-link> 
    <el-button type="danger" plain @click="removeToy">X</el-button>
  </section>
</template>





<script>
// import ToyList from '@/components/ToyList.vue'

export default {
  name: 'ToyDetails',

  created() {
    console.log('details')
    var {toyId} = this.$route.params
    this.$store.dispatch({ type: 'loadToy', toyId}) //{ type: 'ToyStore/loadToy', toyId}
  },

  computed: {
    currToy() {
      return this.$store.getters.currToy //this.$store.getters.currToy
    }
  },

  methods: {
    removeToy() {
      var {toyId} = this.$route.params
      this.$store.dispatch({ type: 'removeToy', toyId})
        .then(() => this.$router.push('/toy')) 
    }
  },

  watch: {
    '$route.params': function() {
        var {toyId} = this.$route.params
        this.$store.dispatch({ type: 'loadToy', toyId})
      }
  }
 
}
</script>
