

<template>
  <div v-if="toysForDisplay" class="toy-app">
    <router-link :to="'/toy/edit'" class="add-toy">
      <el-button type="primary" plain class="btn btn-add">Add new</el-button>
    </router-link> 
    <filter-by :filterBy="filterBy" @filter="loadToys"></filter-by>
    <toy-list :toys="toysForDisplay"/>
  </div>
</template>





<script>

import ToyList from '@/components/ToyList.vue'
import FilterBy from '@/components/FilterBy.vue'
import ToyHeader from '@/components/ToyHeader.vue'


export default {

  name: 'ToyApp',
  components: {
    ToyList,
    FilterBy,
    ToyHeader
  },
  data(){
    return {
      filterBy: {
        name: '',
        type: '',
        inStock: 'all'
      }
    }
  },

  created() {
    this.loadToys()
  },

  computed: {
    toysForDisplay() {
      return this.$store.getters.toysForDisplay //this.$store.getters['ToyStore/toysForDisplay']
    }
  },

  methods: {
    loadToys() {
      this.$store.dispatch({ type: 'loadToys', filterBy: this.filterBy })
        .then(toys => toys)
    }
  },
}
</script>

<style scoped >


  .btn-add {
    background-color: white;
    border-color: #DCDFE6;
    color: #606266;
  }
  .btn-add:hover {
    background-color: #aad296;
    border-color: #DCDFE6;
    color: #606266;
  }

  
  
